.toastify-content {
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(60, 176, 219, 1)),
    color-stop(53%, rgba(60, 176, 219, 0.6)),
    to(rgba(60, 176, 219, 0.3))
  );
  background: -webkit-linear-gradient(
    top,
    rgba(60, 176, 219, 1) 0%,
    rgba(60, 176, 219, 0.6) 53%,
    rgba(60, 176, 219, 0.3) 100%
  );
  background: -o-linear-gradient(
    top,
    rgba(60, 176, 219, 1) 0%,
    rgba(60, 176, 219, 0.6) 53%,
    rgba(60, 176, 219, 0.3) 100%
  );
  background: linear-gradient(
    to bottom,
    rgba(60, 176, 219, 1) 0%,
    rgba(60, 176, 219, 0.6) 53%,
    rgba(60, 176, 219, 0.3) 100%
  );
  border-radius: 5px;
  color: rgb(0, 0, 0);
}

.toastify__body {
  font-size: 15px;
  font-family: 'Noto Serif', serif;
  position: relative;
  text-align: center;
  vertical-align: middle;
}



