.loader {
  font-size: 10px;
  margin: 10px auto;
  text-indent: -9999em;
  width: 6em;
  height: 6em;
  border-radius: 50%;
  background: #d2222d;
  background: -webkit-linear-gradient(
    left,
    #d2222d 10%,
    rgba(210, 34, 45, 0) 42%
  );
  background: -o-linear-gradient(left, #d2222d 10%, rgba(210, 34, 45, 0) 42%);
  background: -webkit-gradient(linear, left top, right top, color-stop(10%, #d2222d), color-stop(42%, rgba(210, 34, 45, 0)));
  background: -webkit-linear-gradient(left, #d2222d 10%, rgba(210, 34, 45, 0) 42%);
  background: linear-gradient(to right, #d2222d 10%, rgba(210, 34, 45, 0) 42%);
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
  animation: load3 1.4s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}
.loader:before {
  width: 50%;
  height: 50%;
  background: #d2222d;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}
.loader:after {
  background: #6f2525;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
@-webkit-keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}



